@import "./reset.scss";

$mainred: #ff0000;

.red {
  color: $mainred;
}
@mixin alertBox() {
  padding: 10px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
}
.my-alert {
  background-color: #eeeeee;
  @include alertBox();
}
.my-alert-red {
  @extend .my-alert;
  background-color: #fdb8b8;
  color: #fff;
}
.my-alert p {
  margin: 0;
}
.product-img {
  max-width: 400px;
}
.hover:hover {
  opacity: 0.8;
}
